import { defineComponent } from "vue";
import AdminLayout from "@/layouts/Admin/index.vue";
import routes from "@/routes";

const faqContent = [
  {
    q: "How can I use data from a previous block?",
    a: "When you connect two blocks, you will have access to the context of all the previous blocks. To use data from another block simply type # and the list of available fields will come up.",
    displayed: false,
  },
  {
    q: "How long will my bot be running?",
    a: "Once activated, your bot will be running indefinitely with the frequency you chose until you switch it off again. You can do that in the <a href='/botos' class='text-white'><u>My Botos</u></a> option.",
    displayed: false,
  },
  {
    q: "Can I create my own block?",
    a: "Yes! Boto is an open platform for developers, if you want to create your own block and make it available to others <a href='/contact' class='text-white'><u>drop us a line</u></a>.",
    displayed: false,
  },
  {
    q: "Are there predefined bots I can use?",
    a: "Yes! You can find some of the available predefined bots in the <a href='/recipes' class='text-white'><u>Recipes</u></a> section.",
    displayed: false,
  },
  {
    q: "My bot has not run when expected, what can I do?",
    a: "If you are having issues with your bit, <a href='/botos' class='text-white'><u>send us a message</u></a> and we will look into it straight away.",
    displayed: false,
  },
  {
    q: "I found a bug or have a suggestion, how can I report it?",
    a: "If you found a bug or have an idea of a cool feature we could implement please <a href='/contact' class='text-white'><u>send us a message</u></a>, we would love to hear from you.",
    displayed: false,
  },
];
export default defineComponent({
  name: "Faq",
  components: {
    AdminLayout,
  },
  data() {
    return {
      faqContent,
    };
  },
  created() {
    if (!navigator.onLine) {
      routes.push({ name: "home.offline", params: { route: this.$route.fullPath } });
    }
  },
  methods: {
    showFaq(index: number) {
      this.faqContent[index].displayed = !this.faqContent[index].displayed;
    },
    expandAll() {
      for (const item of this.faqContent) {
        item.displayed = true;
      }
    },
    collapseAll() {
      for (const item of this.faqContent) {
        item.displayed = false;
      }
    },
  },
});
